/* eslint-disable react/jsx-no-comment-textnodes */

import React from "react";
import { Col } from "react-bootstrap";
import "./Home.css";
import ReactTypingEffect from "react-typing-effect";

const Info = () => {
  function githubHandle() {
    window.open("https://github.com/abdulrahman-kh-adam", "_blank");
  }
  return (
    <Col
      xs="12"
      md="6"
      className="d-flex flex-column justify-content-center flex-wrap info"
    >
      <p style={{ color: "#fff", fontSize: "18px", margin: "0px" }}>
        Hi all. I am
      </p>
      <p
        style={{ color: "#fff", fontSize: "62px", marginTop: "-10px" }}
        className="name"
      >
        Abdulrahman Adam
      </p>
      <p
        style={{ color: "#4D5BCE", fontSize: "32px", marginTop: "-30px" }}
        className="developer"
      >
        &gt; <ReactTypingEffect text={" Front-End Developer"} />
      </p>
      <p
        style={{ color: "#607B96", fontSize: "16px", marginTop: "75px" }}
        className="comment"
      >
        // Go to _about-me to see skills, education, etc...
      </p>
      <p style={{ color: "#607B96", fontSize: "16px", marginTop: "-10px" }}>
        // Checkout my Github page
      </p>
      <p style={{ marginTop: "-10px" }} className="github">
        <span
          style={{
            color: "#4D5BCE",
            paddingRight: "10px",
            fontWeight: "bold",
          }}
        >
          const
        </span>
        <span
          style={{
            color: "#43D9AD",
            paddingRight: "10px",
            fontWeight: "bold",
          }}
        >
          githubLink
        </span>
        <span style={{ paddingRight: "10px" }}>=</span>
        <span
          style={{
            color: "#E99287",
            paddingRight: "10px",
          }}
        >
          "
          <span
            style={{ textDecoration: "underline", cursor: "pointer" }}
            onClick={githubHandle}
            className="link"
          >
            https://github.com/abdulrahman-kh-adam
          </span>
          "
        </span>
      </p>
    </Col>
  );
};

export default Info;
