import "./Projects.css";
import { react_projects } from "../../Components/Projects/ProjectsList";
import { vanilla_projects } from "../../Components/Projects/ProjectsList";
import { other_projects } from "../../Components/Projects/ProjectsList";

import React from "react";
import ProjectCard from "./ProjectCard";

const ProjectsPhone = () => {
  return (
    <div style={{ padding: "10px", width: "100%" }}>
      <div className="react-projects">
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          React Projects
        </h3>
        <div className="projects-container">
          {react_projects.length
            ? react_projects.map((project) => {
                return (
                  <ProjectCard
                    title={project.title}
                    description={project.description}
                    img={project.img}
                    github={project.github}
                    live={project.live}
                  />
                );
              })
            : "No projects available"}
        </div>
      </div>
      <br />
      <div className="vanilla-projects">
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          Vanilla JS Projects
        </h3>
        <div className="projects-container">
          {vanilla_projects.length
            ? vanilla_projects.map((project) => {
                return (
                  <ProjectCard
                    title={project.title}
                    description={project.description}
                    img={project.img}
                    github={project.github}
                    live={project.live}
                  />
                );
              })
            : "No projects available"}
        </div>
      </div>
      <br />
      <div className="other-projects">
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          Other Projects
        </h3>
        <div className="projects-container">
          {other_projects.length
            ? other_projects.map((project) => {
                return (
                  <ProjectCard
                    title={project.title}
                    description={project.description}
                    img={project.img}
                    github={project.github}
                    live={project.live}
                  />
                );
              })
            : "No projects available"}
        </div>
      </div>
    </div>
  );
};

export default ProjectsPhone;
