import React from "react";
import File from "./File";

const Personal = ({ handleActive }) => {
  return (
    <div className="personal-list">
      <File
        name="bio"
        iconColor="#E99287"
        active="bio"
        handleActive={handleActive}
      />
      <File
        name="education"
        iconColor="#43D9AD"
        active="education"
        handleActive={handleActive}
      />
      <File
        name="skills"
        iconColor="#3A49A4"
        active="skills"
        handleActive={handleActive}
      />
    </div>
  );
};

export default Personal;
