import React from "react";
import File from "../About/File";

const Filter = ({ handleActive }) => {
  return (
    <div className="filter-list">
      <File
        name="react-projects"
        iconColor="#E99287"
        active="react-projects"
        handleActive={handleActive}
      />
      <File
        name="vanilla-js-projects"
        iconColor="#43D9AD"
        active="vanilla-js-projects"
        handleActive={handleActive}
      />
      <File
        name="other"
        iconColor="#3A49A4"
        active="other"
        handleActive={handleActive}
      />
    </div>
  );
};

export default Filter;
