import React from "react";
import { Container, Row } from "react-bootstrap";
import Info from "../../Components/Home/Info";
import Picture from "../../Components/Home/Picture";

const Home = () => {
  return (
    <div
      className="homecomp"
      style={{
        height: "calc(100vh - 105px)",
        display: "flex",
        alignItems: "center",
      }}
    >
      <Container>
        <Row>
          <Info />
          <Picture />
        </Row>
      </Container>
    </div>
  );
};

export default Home;
