import React, { useState } from "react";
import "./About.css";
import Personal from "./Personal";
import File from "./File";

const Sidebar = ({ handleActive }) => {
  const [main, setMain] = useState(false);
  const [contact, setContact] = useState(false);

  function handleClickMain() {
    setMain(!main);
  }

  function handleClickContact() {
    setContact(!contact);
  }

  return (
    <div className="sidebar" style={{ height: "calc(100vh - 105px)" }}>
      <div className="sidebar-title" onClick={handleClickMain}>
        {main ? (
          <i class="fa-solid fa-caret-down" />
        ) : (
          <i class="fa-solid fa-caret-right" />
        )}
        <span className="text">personal-info</span>
      </div>
      {main ? <Personal handleActive={handleActive} /> : null}

      <div className="sidebar-title" onClick={handleClickContact}>
        {contact ? (
          <i class="fa-solid fa-caret-down" />
        ) : (
          <i class="fa-solid fa-caret-right" />
        )}
        <span className="text">contact</span>
      </div>
      {contact ? (
        <div className="contact-list">
          <File name="contact" active="contact" handleActive={handleActive} />
        </div>
      ) : null}
    </div>
  );
};

export default Sidebar;
