import React, { useEffect, useRef, useState } from "react";
import SkillCard from "./SkillCard";
import html from "../../Images/Icons/html.png";
import css from "../../Images/Icons/css.png";
import js from "../../Images/Icons/js.png";
import react from "../../Images/Icons/react.png";
import redux from "../../Images/Icons/redux.png";
import bootstrap from "../../Images/Icons/bootstrap.png";
import rest from "../../Images/Icons/rest.png";
import mui from "../../Images/Icons/mui.png";
import c from "../../Images/Icons/c.png";
import cpp from "../../Images/Icons/cpp.png";
import java from "../../Images/Icons/java.png";
import py from "../../Images/Icons/py.png";
import flask from "../../Images/Icons/flask.png";
import mysql from "../../Images/Icons/sql.png";
import emailjs from "@emailjs/browser";

const AboutPhone = () => {
  const form = useRef();
  const name = useRef();
  const email = useRef();
  const message = useRef();
  const [mailStat, setMailStat] = useState(null);

  const sendMail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_vmrxn7v", "template_teunxwu", form.current, {
        publicKey: "m4J5v1JbtN_oOax-r",
      })
      .then(
        () => {
          setMailStat(true);
          console.log("DONE");
        },
        (error) => {
          setMailStat(false);
        }
      );
  };

  useEffect(() => {
    name.current.value = "";
    email.current.value = "";
    message.current.value = "";
  }, [mailStat]);

  return (
    <div style={{ padding: "10px" }}>
      <div className="bio-phone">
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          Personal Information
        </h3>
        <p>
          <span style={{ fontWeight: "bold" }}>Name: </span>Abdulrahman Khaled
          Hussien Adam
        </p>
        <p>
          <span style={{ fontWeight: "bold" }}>Address: </span>Maadi, Cairo,
          Egypt
        </p>
        <p>
          <span style={{ fontWeight: "bold" }}>Phone Number: </span>+2
          01125733974
        </p>
        <br />
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          BIO
        </h3>
        <p>
          Senior mechatronics student interested in software devolopment (web
          development specially). Experienced in front end development using
          React. Currently, I seek to learn not to earn so I am searching for
          good internship opportunities!
        </p>
      </div>

      <br />

      <div className="education-phone">
        <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
          Education
        </h3>
        <h6 style={{ color: "#607B96" }}>2020 - present</h6>
        <h4>Mechatronics Engineering</h4>
        <h6>Faculty of Engineering, Ain Shams University, Cairo, Egypt</h6>
        <span style={{ padding: "5px" }}></span>
        <h6 style={{ color: "#607B96" }}>2017 - 2020</h6>
        <h4>High School</h4>
        <h6>Gulf Private School, Ryiadh, Saudi Arabia</h6>
      </div>

      <br />

      <div className="skills-phone">
        <h3
          style={{
            marginBottom: "15px",
            borderBottom: "1px solid #fea55f",
            width: "fit-content",
          }}
        >
          Skills
        </h3>
        <h5>Front-End Tech Stack:</h5>
        <div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <SkillCard name="HTML" image={html} percentage={90} />
            <SkillCard name="CSS" image={css} percentage={100} />
            <SkillCard name="JavaScript" image={js} percentage={80} />
            <SkillCard name="React JS" image={react} percentage={75} />
            <SkillCard name="React-Redux" image={redux} percentage={50} />
            <SkillCard name="Bootstrap" image={bootstrap} percentage={100} />
            <SkillCard name="RestFUL APIs" image={rest} percentage={100} />
            <SkillCard name="Material UI" image={mui} percentage={40} />
          </div>
        </div>
        <h5>Other Skills:</h5>
        <div>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            <SkillCard name="C" image={c} percentage={80} />
            <SkillCard name="C++" image={cpp} percentage={75} />
            <SkillCard name="Java" image={java} percentage={60} />
            <SkillCard name="Python" image={py} percentage={95} />
            <SkillCard name="Flask" image={flask} percentage={50} />
            <SkillCard name="MySQL" image={mysql} percentage={50} />
          </div>
        </div>
      </div>
      <br />
      <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
        Send Email
      </h3>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <form ref={form} onSubmit={sendMail}>
          <p className="form-label">_name:</p>
          <input
            type="text"
            placeholder="Enter Your Name"
            className="form-input"
            ref={name}
            name="user_name"
          />
          <p className="form-label">_email:</p>
          <input
            type="email"
            placeholder="Enter Your Email"
            className="form-input"
            ref={email}
            name="user_email"
          />
          <p className="form-label">_message:</p>
          <textarea
            placeholder="Enter Your Message"
            rows="4"
            cols="24"
            className="form-input"
            ref={message}
            name="message"
          ></textarea>
          <br />
          <input className="form-btn" type="submit" value="_submit-message" />
          {mailStat ? <div>Mail sent successfuly!</div> : null}
        </form>
      </div>
      <br />
    </div>
  );
};

export default AboutPhone;
