import Filter from "./Filter";
import "./Projects.css";

import React, { useState } from "react";

const Sidebar = ({ handleActive }) => {
  const [main, setMain] = useState(false);

  function handleClickMain() {
    setMain(!main);
  }

  return (
    <div className="sidebar" style={{ height: "calc(100vh - 105px)" }}>
      <div className="sidebar-title" onClick={handleClickMain}>
        {main ? (
          <i class="fa-solid fa-caret-down" />
        ) : (
          <i class="fa-solid fa-caret-right" />
        )}
        <span className="text">my-projects</span>
      </div>
      {main ? <Filter handleActive={handleActive} /> : null}
    </div>
  );
};

export default Sidebar;
