import React from "react";
import { Container, Row } from "react-bootstrap";
import SkillCard from "./SkillCard";
import "./About.css";
import html from "../../Images/Icons/html.png";
import css from "../../Images/Icons/css.png";
import js from "../../Images/Icons/js.png";
import react from "../../Images/Icons/react.png";
import redux from "../../Images/Icons/redux.png";
import bootstrap from "../../Images/Icons/bootstrap.png";
import rest from "../../Images/Icons/rest.png";
import mui from "../../Images/Icons/mui.png";
import c from "../../Images/Icons/c.png";
import cpp from "../../Images/Icons/cpp.png";
import java from "../../Images/Icons/java.png";
import py from "../../Images/Icons/py.png";
import flask from "../../Images/Icons/flask.png";
import mysql from "../../Images/Icons/sql.png";

const Skills = () => {
  return (
    <div className="skills">
      <h3
        style={{
          marginBottom: "15px",
          borderBottom: "1px solid #fea55f",
          width: "fit-content",
        }}
      >
        Skills
      </h3>
      <h5>Front-End Tech Stack:</h5>
      <Container>
        <Row>
          <SkillCard name="HTML" image={html} percentage={90} />
          <SkillCard name="CSS" image={css} percentage={100} />
          <SkillCard name="JavaScript" image={js} percentage={80} />
          <SkillCard name="React JS" image={react} percentage={75} />
        </Row>
        <Row>
          <SkillCard name="React-Redux" image={redux} percentage={50} />
          <SkillCard name="Bootstrap" image={bootstrap} percentage={100} />
          <SkillCard name="RestFUL APIs" image={rest} percentage={100} />
          <SkillCard name="Material UI" image={mui} percentage={40} />
        </Row>
      </Container>
      <h5>Other Skills:</h5>
      <Container>
        <Row>
          <SkillCard name="C" image={c} percentage={80} />
          <SkillCard name="C++" image={cpp} percentage={75} />
          <SkillCard name="Java" image={java} percentage={60} />
          <SkillCard name="Python" image={py} percentage={95} />
        </Row>
        <Row>
          <SkillCard name="Flask" image={flask} percentage={50} />
          <SkillCard name="MySQL" image={mysql} percentage={50} />
        </Row>
      </Container>
    </div>
  );
};

export default Skills;
