import React, { useEffect, useRef, useState } from "react";
import emailjs from "@emailjs/browser";

const Contact = () => {
  const form = useRef();
  const name = useRef();
  const email = useRef();
  const message = useRef();
  const [mailStat, setMailStat] = useState(null);

  const sendMail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm("service_vmrxn7v", "template_teunxwu", form.current, {
        publicKey: "m4J5v1JbtN_oOax-r",
      })
      .then(
        () => {
          setMailStat(true);
          console.log("DONE");
        },
        (error) => {
          setMailStat(false);
        }
      );
  };

  useEffect(() => {
    name.current.value = "";
    email.current.value = "";
    message.current.value = "";
  }, [mailStat]);

  return (
    <div>
      <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
        Contact
      </h3>
      <br />
      <p>
        <span style={{ fontWeight: "bold" }}>Email: </span>
        abdulrahmankhadamm@gmail.com
      </p>
      <p>
        <span style={{ fontWeight: "bold" }}>Linked In: </span>
        <a
          href="https://www.linkedin.com/in/abdulrahmankhadamm/"
          style={{ color: "white" }}
        >
          https://www.linkedin.com/in/abdulrahmankhadamm/
        </a>
      </p>
      <p>
        <span style={{ fontWeight: "bold" }}>Phone Number: </span>
        +2 01125733974
      </p>
      <p>
        <span style={{ fontWeight: "bold" }}>Address: </span>
        Maadi, Cairo, Egypt
      </p>
      <br />
      <h3 style={{ borderBottom: "1px solid #fea55f", width: "fit-content" }}>
        Send Email
      </h3>
      <br />
      <form ref={form} onSubmit={sendMail}>
        <p className="form-label">_name:</p>
        <input
          type="text"
          placeholder="Enter Your Name"
          className="form-input"
          ref={name}
          name="user_name"
        />
        <p className="form-label">_email:</p>
        <input
          type="email"
          placeholder="Enter Your Email"
          className="form-input"
          ref={email}
          name="user_email"
        />
        <p className="form-label">_message:</p>
        <textarea
          placeholder="Enter Your Message"
          rows="4"
          cols="24"
          className="form-input"
          ref={message}
          name="message"
        ></textarea>
        <br />
        <input className="form-btn" type="submit" value="_submit-message" />
        {mailStat ? <div>Mail sent successfuly!</div> : null}
      </form>
    </div>
  );
};

export default Contact;
