import React from "react";
import { Button, Card } from "react-bootstrap";

const ProjectCard = ({ title, description, img, github, live }) => {
  return (
    <Card style={{ width: "18rem" }} className="card">
      <img alt="" width="285" height="180" src={img} className="card-img" />
      <Card.Body>
        <Card.Title className="card-title">{title}</Card.Title>
        <Card.Text className="card-body">{description}</Card.Text>
        {github ? (
          <Button className="card-btn" variant="primary" href={github}>
            GitHub
          </Button>
        ) : null}
        {live ? (
          <Button className="card-btn" variant="primary" href={live}>
            Live preview
          </Button>
        ) : null}
      </Card.Body>
    </Card>
  );
};

export default ProjectCard;
