import React from "react";
import { Col } from "react-bootstrap";
import image from "../../Images/3d.webp";
import "./Home.css";

const Picture = () => {
  return (
    <Col
      xs="12"
      md="6"
      className="d-flex align-items-center justify-content-center picture"
    >
      <div className="gblock"></div>
      <div className="bblock"></div>
      <img
        src={image}
        alt=""
        style={{ position: "absolute", zIndex: "3" }}
        width="500px"
        height="500px"
        className="img"
      />
    </Col>
  );
};

export default Picture;
