import React from "react";
import "./Header.css";
import Nav from "../Nav List/Nav";

const Header = ({ handlePhoneMenu }) => {
  return (
    <div className="header d-flex">
      <div className="title">abdulrahman-adam</div>
      <div className="nav">
        <Nav />
        <i className="fa-solid fa-bars menu" onClick={handlePhoneMenu} />
      </div>
    </div>
  );
};

export default Header;
